
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 48px;
  width: 100%;
  z-index: 50;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
}
.shopcart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shopcart .content .content-left .icon-wrapper {
  display: inline-block;
  position: relative;
  z-index: 100;
  top: -10px;
  left: 12px;
  width: 56px;
  height: 56px;
  border-radius: 56px;
  background-color: #141d27;
}
.shopcart .content .content-left .icon-wrapper .badge {
  position: absolute;
  top: 0;
  left: 26px;
  font-size: 12px;
  line-height: 16px;
  padding: 0 6px;
  color: #fff;
  background-color: #f01414;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
  border-radius: 18px;
}
.shopcart .content .content-left .icon-wrapper .icon {
  width: 44px;
  height: 44px;
  border-radius: 44px;
  margin: 6px;
  z-index: 200;
  background-color: #2b343c;
  text-align: center;
}
.shopcart .content .content-left .icon-wrapper .icon .icon-shopping_cart {
  font-size: 24px;
  color: rgba(255,255,255,0.4);
  line-height: 44px;
}
.shopcart .content .content-left .icon-wrapper .icon.active {
  background-color: #00a0dc;
}
.shopcart .content .content-left .icon-wrapper .icon.active .icon-shopping_cart {
  color: #fff;
}
.shopcart .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  margin: 12px 12px 12px 18px;
  padding-right: 12px;
  font-size: 16px;
  font-weight: 700;
  color: rgba(255,255,255,0.4);
  line-height: 24px;
  border-right: 1px solid rgba(255,255,255,0.1);
}
.shopcart .content .content-left .desc {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
  color: rgba(255,255,255,0.4);
  font-weight: 200;
  line-height: 48px;
}
.shopcart .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  width: 105px;
  background-color: #2b343c;
  font-size: 14px;
  line-height: 48px;
  font-weight: 700;
  text-align: center;
  color: rgba(255,255,255,0.4);
}
.shopcart .content .content-right.active {
  background-color: #00b43c;
  color: #fff;
}
.shopcart .shopcart-list {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background: #fff;
  z-index: -1;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.shopcart .shopcart-list.transHeight-enter-active,
.shopcart .shopcart-list.transHeight-leave-active {
  transition: all 0.5s;
}
.shopcart .shopcart-list.transHeight-enter,
.shopcart .shopcart-list.transHeight-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shopcart .shopcart-list .list-header {
  height: 40px;
  width: 100%;
  background: #f3f5f7;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  line-height: 40px;
}
.shopcart .shopcart-list .list-header .title {
  display: inline-block;
  margin-left: 18px;
  font-size: 14px;
  font-weight: 200;
  color: #07111b;
}
.shopcart .shopcart-list .list-header .empty {
  position: absolute;
  right: 8px;
  padding: 0 10px;
  font-size: 12px;
  color: #00a0dc;
}
.shopcart .shopcart-list .list-content {
  font-size: 0;
  max-height: 217px;
  overflow: hidden;
}
.shopcart .shopcart-list .list-content .food {
  position: relative;
  margin: 0 18px;
  position: relative;
}
.shopcart .shopcart-list .list-content .food:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.shopcart .shopcart-list .list-content .food .name {
  font-size: 14px;
  color: #07111b;
  line-height: 48px;
  font-weight: 700;
}
.shopcart .shopcart-list .list-content .food .price {
  position: absolute;
  right: 90px;
  bottom: 0;
  font-size: 10px;
  line-height: 48px;
  color: #f01414;
  padding: 0 12px 0 18px;
}
.shopcart .shopcart-list .list-content .food .price > span {
  font-size: 14px;
  font-weight: 700;
  line-height: 48px;
}
.shopcart .shopcart-list .list-content .food .cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 6px;
  font-size: 24px;
  margin-top: 6px;
}
.shopcart .backdrop {
  background-color: rgba(7,17,27,0.6);
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -2;
}
.shopcart .backdrop.fade-enter-active,
.shopcart .backdrop.fade-leave-active {
  transition: all 0.5s;
}
.shopcart .backdrop.fade-enter,
.shopcart .backdrop.fade-leave-active {
  opacity: 0;
}
.shopcart .ball-container .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
}
.shopcart .ball-container .ball.drop-enter,
.shopcart .ball-container .ball.drop-enter-active {
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.shopcart .ball-container .ball.drop-enter .inner,
.shopcart .ball-container .ball.drop-enter-active .inner {
  width: 16px;
  height: 16px;
  background-color: #00a0dc;
  border-radius: 50%;
  transition: all 0.4s linear;
}


