
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
  transition: all 0.4s;
}
.cartcontrol .cart-decrease .icon-remove_circle_outline {
  display: inline-block;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
  transition: all 0.4s;
}
.cartcontrol .cart-decrease.move-transition {
  opacity: 1;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
}
.cartcontrol .cart-decrease.move-transition .icon-remove_circle_outline {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartcontrol .cart-decrease.move-enter,
.cartcontrol .cart-decrease.move-leave-to {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
}
.cartcontrol .cart-decrease.move-enter .icon-remove_circle_outline,
.cartcontrol .cart-decrease.move-leave-to .icon-remove_circle_outline {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  line-height: 24px;
  padding: 6px 0;
  width: 12px;
  color: #4d555d;
  text-align: center;
}
.cartcontrol .cart-add {
  padding: 6px;
  display: inline-block;
}
.cartcontrol .cart-add .icon-add_circle {
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}


