
.classify {
  font-size: 0;
  padding: 18px 0;
  margin: 0 18px;
  position: relative;
}
.classify:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.classify>span {
  display: inline-block;
  font-size: 12px;
  margin-right: 8px;
  padding: 8px 12px;
}
.classify .all {
  background-color: rgba(0,160,220,0.2);
  color: #4d555d;
}
.classify .all.active {
  background-color: #00a0dc;
  color: #fff;
}
.classify .good {
  background-color: rgba(0,160,220,0.2);
  color: #4d555d;
}
.classify .good.active {
  background-color: #00a0dc;
  color: #fff;
}
.classify .bad {
  background-color: rgba(77,85,93,0.2);
  color: #4d555d;
}
.classify .bad.active {
  background-color: #4d555d;
  color: #fff;
}
.switch {
  padding: 12px 18px;
  font-size: 12px;
  line-height: 24px;
  color: #93999f;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.switch .icon {
  display: inline-block;
  font-size: 24px;
  vertical-align: top;
  margin-left: 4px;
}
.switch .icon .icon-check_circle {
  color: #93999f;
}
.switch .icon .icon-check_circle.on {
  color: #00c850;
}


